.input {
    position: absolute;
    opacity: 0;
    z-index: -1;
}
.row {
    display:flex;
    
    }
.row .col {
    flex:1;
    
}
.row .col:last-child {
    margin-left: 1em;
}
.tabs {
    border-radius: 8px;
    overflow: hidden;
    box-shadow: 0 4px 4px -2px rgba(0,0,0,0.5);
}
.tab {
    width: 100%;
    color: white;
    overflow: hidden;
    }
.tablabel {
    display: flex;
    justify-content: space-between;
    padding: 1em;
    background: #2c3e50;
    font-weight: bold;
    cursor: pointer;
    /* Icon */
}
.tablabel:hover {
    background: #1a252f;
}
.tablabel::after {
    content: "‚ùØ";
    width: 1em;
    height: 1em;
    text-align: center;
    transition: all 0.35s;
}
.tabcontent {
    max-height: 0;
    padding: 0 1em;
    color: #2c3e50;
    background: white;
    transition: all 0.35s;
}
.tab-close {
    display: flex;
    justify-content: flex-end;
    padding: 1em;
    font-size: 0.75em;
    background: #2c3e50;
    cursor: pointer;
}
.tab-close:hover {
    background: #1a252f;
}

input:checked + .tablabel {
    background: #1a252f;
}
    input:checked + .tablabel::after {
    transform: rotate(90deg);
}
input:checked ~ .tabcontent {
    max-height: 100vh;
    padding: 1em;
}
